body { margin:0; padding:0; font-family: 'Poppins', sans-serif; background: url(/r/arkaplan.jpg) center center no-repeat; background-size:110% 110%!important; animation: arkaplan 60s cubic-bezier(0.65, 0.05, 0.36, 1) infinite; }

@keyframes arkaplan {
    0% { background-position: 0 center; }
    50% { background-position: 100% center; }
    100% { background-position: 0 center; }
}

input:-internal-autofill-selected {
    background: red!important;
}

#root {
    width:100vw;
    height:100vh;
    display:flex;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.55);
}

.container {
    max-width: 90vw;
    max-height:90vh;
    margin:0 auto;
    background: #fff;
    padding: 4px;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.85)!important;
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    overflow: hidden!important;
}

.baslik {
    width:calc(calc(100% + 8px) - 16px);
    padding:0 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background:linear-gradient(to bottom, #f4f4f4, transparent);
    position: relative;
    top:-4px;
    left:-4px;
}

.baslik h3 {
    font-size:18px;
    font-weight: bold;
    color:#223394;
    user-select: none;
}

.routeContainer {
    width:100%;
    max-width: 90vw;
    height:100%;
    max-height:90vh;
    display: flex;
    flex-direction: column
}

.girisYap form {
    width:calc(100% - 8px);
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 4px;
}
.girisInput {
    width:100%;
    margin-bottom:15px!important;
}

.girisButon {
    width:100%;
}

.girisUyari {
    width:100%;
    font-size:13px;
    font-weight: bold;
    color:red;
    text-align: center;
    margin:0 0 15px;
}

.siparisEkle {
    width:calc(100% - 8px);
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 4px;
}

.siparisEkle form {
    width:calc(100%);
}

.siparisEkle form tr {
    width:calc(100%);
    display:flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 15px;
}

.siparisListe {
    flex:1;
    overflow-y: scroll;
    overflow-x: hidden;
    scrollbar-width:thin;
}
.siparisListeHead tr th { height: 48px; background: linear-gradient(to bottom, #fff, #fafafade); backdrop-filter: blur(5px); -webkit-backdrop-filter: blur(5px); font-weight: bold; font-size: 14px; color:#555; }
.siparisListe .adSoyad { min-width: 130px; }
.siparisListe .kargoAdresi { min-width: 150px; }
.siparisListe .eklenmeTarihi { min-width: 105px; }

.siparisBulunamadÄ± {
    width:100%;
    display: block;
    text-align: center;
    font-size:13px;
}

::-webkit-scrollbar{width:4px;height:4px;}
::-webkit-scrollbar-button{width:0px;height:0px;}
::-webkit-scrollbar-thumb{background:#303F9F;border:0px none #000;}
::-webkit-scrollbar-thumb:hover{background:#000;}
::-webkit-scrollbar-thumb:active{background:#000000;}
::-webkit-scrollbar-track{background:#f4f4f4;border:0px none #000;}
::-webkit-scrollbar-track:hover{background:#f4f4f4;}
::-webkit-scrollbar-track:active{background:#f4f4f4;}
::-webkit-scrollbar-corner{background:transparent;}